body {
  font-family: Arial, sans-serif;
  background: #f8f9fa;
  margin: 0;
  padding: 0;
}


.header {
  background: #004aad;
  color: white;
  padding: 16px;
  font-size: 18px;
  font-weight: bold;
  text-align: center
}

.topnav input[type=text] {
  padding: 6px;
  margin-top: 8px;
  font-size: 17px;
  border: 1px solid #000
}

.topnav .search-container button {

  padding: 6px 10px;
  margin-top: 8px;
  margin-right: 16px;
  background: #ddd;
  font-size: 17px;
  border: none;
  cursor: pointer;
}

.topnav .search-container button:hover {
  background: #ccc;
}

.todo-app {
  max-width: 500px;
  margin: 20px auto;
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
}

.search-box {
  padding: 12px;
  border-bottom: 1px solid #eee;
}

.search-box input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 6px;
}

.section {
  border-bottom: 1px solid #eee;
} 
.iconRow {
  display: 'flex';
  gap: '10px';
  margin-top: '8px',
};

.section-header {
  padding: 14px;
  background: #f1f1f1;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.section-header span {
  color: #333;
  font-weight: normal;
}

.task-card {
  display: flex;
  align-items: flex-start;
  padding: 12px;
  border-top: 1px solid #eee;
}

.avatar {
  background: #f0f6ff;
  border: 1px solid #004aad;
  color: #004aad;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
  font-weight: bold;
}

.task-info {
  flex: 1;
}

.task-info h4 {
  margin: 0;
  color: #004aad;
}

.task-info p {
  margin: 4px 0;
  font-size: 14px;
  color: #555;
}

.task-info small {
  color: #777;
}

.task-status {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #444;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 6px;
  display: inline-block;
}

.dot.in-progress {
  background: orange;
}

.dot.pending {
  background: red;
}

.dot.completed {
  background: green;
}

.arrow {
  font-size: 12px;

  float: right;
  margin-bottom: 15px;
}
.edit-cancel {
  background: white;
  border: 1px solid #0057b7;
  color: #0057b7;
  padding: 10px 20px;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: 0.2s ease;
}

.edit-cancel:hover {
  background-color: #f0f7ff;
}

.edit-update {
  background-color: #0057b7;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: 0.2s ease;
}

.edit-update:hover {
  background-color: #0046a0;
}
.flex-row{
  display: block;

}